<template>
	<view class="page bgwhite">
		<view class="list" v-if="!isGet">
			<view class="list-item ch-flex" v-for="item in detailData" :key="item.id">
				<view class="label">{{ item.title }}：</view>
				<view class="ch-flex-1 value"> {{ item.text }}</view>
			</view>
		</view>
		<template v-else>
			<u-loading-icon text="loading"></u-loading-icon>
		</template>
	</view>
</template>

<script>
import Api from "@/api/device/index.js"
export default {
	data() {
		return {
			isGet: true,
			detailData: [],
		};
	},

	onLoad(options) {
		uni.$u.http.get(Api.detail + "machineNo=" + options.machineNo).then(result => {
			console.log('result', result);
			this.isGet = false
			if (result.code === 200) {
				this.detailData = result.data
			}
		}).catch(er => {
			console.log('er', er);
		}).finally(() => {
			this.isGet = false
		})
	},
	onShow() {
		uni.setNavigationBarTitle({ // 修改头部标题
			title: this.$t("machine.detail")
		});
	},
	computed: {

	}
}
</script>

<style lang="scss" scoped>
.list {
	&-item {
		padding: 20upx 24upx;
		@include after();
		font-size: 28upx;
		border: 0;

		.label {
			width: auto;
			color: #333;
			font-weight: 500;
		}

		.value {
			color: #666;
		}
	}
}
</style>